<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <div class="create-post">
      <label for="create">Say Something...</label>
      <input type="text" id="Create-post" v-model="text" placeholder="Create a post">
      <button v-on:click="postCreation">Post!</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="post-container">
      <div class="post" v-for="post in posts" :key="post._id">
        {{ `${new Date(post.createdAt).getDate()}/${new Date(post.createdAt).getMonth() + 1}/${new Date(post.createdAt).getFullYear()}` }}
        <input type="text" v-model="post.text" @blur="updatePost(post._id, post.text)">
        <button v-on:click="postDeletion(post._id)">Delete</button>
        <button v-on:click="updatePost(post._id, post.text)">Update</button>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try {
      this.posts = await PostService.postRetrieval();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async postCreation() {
      await PostService.postInsertion(this.text);
      this.text = ''; // Clear input after posting
      this.posts = await PostService.postRetrieval();
    },
    async postDeletion(id) {
      await PostService.postDeletion(id);
      this.posts = await PostService.postRetrieval();
    },
    async updatePost(id, text) {
      await PostService.postUpdate(id, text);
      this.posts = await PostService.postRetrieval();
    }
  }
}
</script>

<style scoped>
div.container {
  max-width: 800px;
  margin: 0 auto;
}

p.error {
  border: 1px solid #007bff;
  background-color: #cce5ff;
  padding: 10px;
  margin-bottom: 15px;
}

div.post {
  position: relative;
  border: 1px solid #007bff;
  background-color: #e1f5fe;
  padding: 10px 10px 30px 10px;
  margin-bottom: 15px;
}

p.text {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}
</style>

