import axios from 'axios';

const url = 'api/posts/';

class PostService {
    
    //Retrieve posts
    static postRetrieval() {
        return axios.get(url).then(res => res.data.map(post => ({
            ...post,
            createdAt: new Date(post.createdAt)
        }))).catch(err => {
            throw err;
        });
    }

    //Insert posts
    static postInsertion(text) {
        return axios.post(url, { text });
    }

    //Delete post
    static postDeletion(id) {
        return axios.delete(`${url}${id}`);
    }

    // Update Post
    static postUpdate(id, text) {
    return axios.put(`${url}${id}`, { text });
    }
  
}


export default PostService;